import "swiper/css";
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/effect-fade';

import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, EffectFade, Pagination } from "swiper/modules";
import styled from "styled-components";
import { useNavigate } from "react-router-dom";
import { useRef, useState } from "react";

export default function VisualMain() {
  const navigate = useNavigate();
  const [show, setShow] = useState(false);
  const swiper = useRef(null);

  const SlideStyle = [
    {
      backgroundImage: `url(${process.env.PUBLIC_URL+"/img/visual_main_01.jpg"})`
    },
    {
      paddingTop: "170px",
      backgroundImage: `url(${process.env.PUBLIC_URL+"/img/visual_main_02.jpg"})`,
      textAlign: "center"
    },
    {
      backgroundImage: `url(${process.env.PUBLIC_URL+"/img/visual_main_03.jpg"})`,
    }
  ]

  const EnterBtn = styled.button`
    margin-top: 30px;
    padding: 5px 15px;
    border: 1px solid ${(props) => props.color ? props.color : "black"};
    border-radius: 15px;
    background: none;
    color: ${(props) => props.color ? props.color : "black"};
  `

  const SwiperBtn = styled.span`
    width: var(--swiper-navigation-size);
    height: var(--swiper-navigation-size);
    padding: 30px;
    border-radius: 50%;
    background-color: var(--point-color);
    color: white;
  `

  return (
    <div onMouseEnter={() => setShow(true)} onMouseLeave={() => setShow(false)}>
      <Swiper className="visual-main"
        ref={swiper}
        modules={[Pagination, Autoplay, EffectFade]}
        pagination={{
          clickable: true
        }}
        autoplay={{
          delay: 4000
        }}
        loop
        effect={"fade"}
        speed={500}
        onInit={(s) => {
          swiper.current = s;
        }}
      >
        <SwiperSlide style={SlideStyle[0]}>
          <p style={{fontSize: "20px"}}>
             X 
          </p>
          <p style={{fontSize: "32px", marginBottom: "10px"}}>
               !
          </p>
          <p>     <br/>     </p>
          <EnterBtn onClick={() => navigate("/coffee/nola-blend-v")}> </EnterBtn>
        </SwiperSlide>

        <SwiperSlide style={SlideStyle[1]}>
          <p style={{fontSize: "20px"}}>
               <br/>     !
          </p>
          <EnterBtn> </EnterBtn>
        </SwiperSlide>

        <SwiperSlide style={SlideStyle[2]}>
          <p style={{fontSize: "32px", marginBottom: "10px"}}>
                  
          </p>
          <p style={{marginBottom: "5px"}}>
               ,   .
          </p>
          <p>  150     100   <br/> 41,000    .</p>
          <EnterBtn onClick={() => navigate("/list/coffee")}> </EnterBtn>
        </SwiperSlide>
        <SwiperBtn 
          className={"swiper-button-prev" + (!show ? " swiper-button-hidden" : "")} 
          onClick={() => swiper.current.slidePrev()} 
        />
        <SwiperBtn 
          className={"swiper-button-next" + (!show ? " swiper-button-hidden" : "")} 
          onClick={() => swiper.current.slideNext()} 
        />
      </Swiper>
    </div>
  )
}