import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";

import coffeeData from "./data/coffees";
import recipeData from "./data/recipes";
import Coffee from "./Coffee";
import Recipe from "./Recipe";
import VisualMain from "./VisualMain";

export default function Home() {
  const [coffees] = useState(coffeeData.slice(0, 12));
  const [recipes] = useState(recipeData.slice(0, 4));
  const navigate = useNavigate();

  const BannerEnterBtn = styled.button`
    width: 120px;
    height: 120px;
    margin: 40px 20px;
    border: none;
    border-radius: 50%;
    background-color: var(--point-color);
    color: white
  `

  return (
    <div className="home">
      <VisualMain/>

      <TitleBar title=" " path="/list/coffee" />
      <div className="coffee-list">
        {coffees.map((coffee) => <Coffee coffee={coffee} />)}
      </div>
      
      <div className="banner">
        <div className="banner-img">
          <img src={process.env.PUBLIC_URL+"/img/banner_book.avif"} alt="7 " />
        </div>
        <div className="banner-txt">
          <p style={{fontSize: "24px", marginBottom: "10px"}}>7   - </p>
          <p style={{fontSize: "18px"}}>       7.<br/>  ,        .</p>
        </div>
        <BannerEnterBtn onClick={() => navigate("/letter")}>
           <br/>
        </BannerEnterBtn>
      </div>

      <TitleBar title=" " path="/list/recipe" />
      <div className="recipe-list">
        {recipes.map((recipe) => <Recipe recipe={recipe} />)}
      </div>
    </div>
  )
}

function TitleBar(props) {
  const {title, path} = props;
  const navigate = useNavigate();

  const EnterBtn = styled.button`
    padding: 3px 15px;
    border: 1px solid var(--point-color);
    border-radius: 10px;
    background: none;
    color: var(--point-color);
    font-size: 14px;
  `

  return (
    <div className="title-bar">
      <p>{title}</p>
      <EnterBtn onClick={() => navigate(path)}> </EnterBtn>
    </div>
  )
}